import { Component, h, State, Element } from "@stencil/core";
import tailwind from '../../output.css';
@Component({
    tag: 'rf-read',
    shadow: true
})
export class Read{
    @Element() element: HTMLElement;

    @State() guests: string;
    @State() checkin: string;
    @State() checkout: string;

    componentWillLoad() {
        const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
      const params = new URLSearchParams(window.location.search);
      this.guests = params.get('check_availability') || 'No param found';
      this.checkin = params.get('check_in') || 'No param found';
      this.checkout = params.get('check_out') || 'No param found';
    }
    render(){
        return(<div class='flex flex-row justify-center gap-5 mb- text-xl font-semibold'>
            <h2>Number of guests: {this.guests}</h2>
            <h2>From: {this.checkin} to: {this.checkout}</h2>
        </div>)
    }
}